import React, { useState } from "react"

function ProductView({ product }) {

  const [options, setOptions] = useState({
    frame: 'Black',
    glass: 'UV70'
  });

  const updateOptions = (event) => {
    // console.log(event.target, event.tar)
    setOptions({
      ...options,
      [event.target.name]: event.target.value
    })
  }

  return (
      <main>
        <h1>{product.name}</h1>
        <p>SKU: {product.meta.sku}</p>
        <div>
          Image:

        </div>
        <select name="frame" id="frame" onChange={updateOptions} value={options.frame}>
          <option>Select one...</option>
          <option value="Black">Black</option>
          <option value="White">White</option>
          <option value="Brown">Brown</option>
        </select>

        
        <select name="glass" id="glass" onChange={updateOptions} value={options.glass}>
          <option>Select one...</option>
          <option value="Standard">Standard</option>
          <option value="UV70">UV70</option>
        </select>
        <button className="snipcart-add-item"
          data-item-id={product.meta.id}
          data-item-price={product.price}
          data-item-description={product.description}
          data-item-image="/assets/images/starry-night.jpg"
          data-item-name={product.name}
          data-item-custom1-type="readonly"
          data-item-custom1-name="Frame color"
          data-item-custom1-options="Black|White"
          data-item-custom1-value={options.frame}
          data-item-custom2-name="Glass type"
          data-item-custom2-options="Standard|UV70[+25]"
          data-item-custom2-type="readonly"
          data-item-custom2-value={options.glass}
        >
          Add to cart
        </button>
        <button className="snipcart-checkout">Click here to checkout</button>
        <pre>{JSON.stringify(product)}</pre>
      </main>
  )
}

export default ProductView